import React, {Component} from 'react';
import Dot from '../Dot';
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../../../utils/Amplitude";
import VideoPhone from "./VideoPhone";
import VideoButton from "./VideoButton";
import Controller from "../../media/Controller";

export default class VideoPhoneContainer extends Component {
    constructor(props) {
        super(props);

        this.dotsIds = [];

        this.buttonNextId = nextId();
        this.buttonPreviousId = nextId();
        this.phoneId = nextId();
        this.dotsMenuId = nextId();
        this.phoneContainerId = nextId();
        this.isAutomaticallySliding = true;

        for (let element of this.props.array) {
            this.dotsIds[this.dotsIds.length] = nextId();
        }

        this.state = {
            active_id: 0
        };
    }

    getListOfDots() {
        return this.dotsIds.map(element =>
            <Dot
                id={element}
                isActive={this.dotsIds.indexOf(element) === this.state.active_id }/>
        );
    }

    automaticallySlide() {
        let link = this;
        setInterval(function() {
                if (link.isAutomaticallySliding) {
                    link.slidePhoneBySwipe(1);
                }
            },
            6000
        )
    }

    componentDidMount() {
        let link = this;
        this.automaticallySlide();

        let phoneContainer = document.getElementById(this.phoneContainerId);
        this.xDown = null;
        this.yDown = null;

        phoneContainer.addEventListener('touchstart', function (e) {
            link.handleTouchStart(e)
        });

        phoneContainer.addEventListener('touchmove', function (e) {
            link.handleTouchMove(e)
        });

        let buttonNext = document.getElementById(this.buttonNextId);
        let buttonBack = document.getElementById(this.buttonPreviousId);

        buttonNext.addEventListener('click', function(e) {
            e.preventDefault();
            link.isAutomaticallySliding = false;
            link.slidePhoneBySwipe(1);
        });

        buttonBack.addEventListener('click', function(e) {
            e.preventDefault();
            link.isAutomaticallySliding = false;
            link.slidePhoneBySwipe(-1);
        });

        for (let id of this.dotsIds) {
            let element = document.getElementById(id);

            element.addEventListener('click', function (e) {
                e.preventDefault();

                sendAmplitudeData("PhoneContainer: clicked on another phone by dot");

                link.setState({
                    active_id: link.dotsIds.indexOf(element.id)
                })
            })
        }
    }

    handleTouchStart(evt) {
        const firstTouch = evt.touches[0];
        this.xDown = firstTouch.clientX;
        this.yDown = firstTouch.clientY;
    };

    handleTouchMove(evt) {
        if (!this.xDown || !this.yDown) {
            return;
        }

        let xUp = evt.touches[0].clientX;
        let yUp = evt.touches[0].clientY;

        let xDifference = this.xDown - xUp;
        let yDifference = this.yDown - yUp;

        if (Math.abs(xDifference) > Math.abs(yDifference)) {
            let sign = xDifference > 0? -1 : 1;

            this.isAutomaticallySliding = false;

            sendAmplitudeData("PhoneContainer: clicked on another phone by swipe");
            this.slidePhoneBySwipe(sign);
        }

        this.xDown = null;
        this.yDown = null;
    };

    slidePhoneBySwipe(sign) {
        let currentIndex = this.state.active_id;
        currentIndex += sign;

        if (currentIndex < 0) {
            currentIndex = this.dotsIds.length - 1;
        }

        if (currentIndex > this.dotsIds.length - 1) {
            currentIndex = 0;
        }

        this.setState({
            active_id: currentIndex
        })
    }

    render() {
        let url = this.props.array[this.state.active_id][this.props.quality].url;
        console.log("Url: " + url);

        let isMobile = false;

        return (
            <div className={"functional__phone_container"} id={this.phoneContainerId}>
                <div className={"functional__phone_container__upper"}>
                    {
                        isMobile?
                            <Controller isLeft={true} id={this.buttonPreviousId} />
                            :
                            <VideoButton isNext={false} id={this.buttonPreviousId}/>
                    }

                    <VideoPhone id={this.phoneId} src={url} />

                    {
                        isMobile ?
                            <Controller isLeft={false} id={this.buttonNextId}/>
                            :
                            <VideoButton isNext={true} id={this.buttonNextId}/>
                    }
                </div>

                <div className={"functional__dots_holder"}>
                    <div className={"functional__dots_menu"} id={this.dotsMenuId}>
                        {(this.props.array.length > 1)? this.getListOfDots() : []}
                    </div>
                </div>
            </div>
        )
    }
}